:not(input):not(textarea):not([contenteditable]) {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.load-more-view {
  position: absolute;
  top: 10%;
  left: 0;
  right: 0;
  height: 100%;
  overflow: hidden;
  background-color: #EFEFF4;
  display: flex;
  flex-direction: column;
}
.load-more-view h1,
.load-more-view h2 {
  margin: 0;
  background-color: #333;
  color: #eee;
  font-size: 1em;
  line-height: 3;
}
.load-more-view h1 a,
.load-more-view h2 a {
  color: inherit;
  margin: 0 1em;
}
.load-more-view .main {
  flex: 1 1;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.load-more-view .main ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.load-more-view .main ul li {
  background-color: #fff;
  border-bottom: 1px solid #ccc;
}
.load-more-view .main ul li p {
  margin: 0;
  line-height: 3em;
}
